import React from 'react';
import s from './Post.module.scss'

const Post = (props) => {
  return (
    <div className={s.item}>
      <img src="data:image/png;base64," alt=""/>
      {props.message}
      <div>
        <span>{props.likesCount}</span>
      </div>
    </div>
  );
};

export default Post;